.cards-list__title {
  position: relative;
  margin-left: 65px;
  font-size: 30px;
  line-height: 36px;
  color: #238441; }
  .cards-list__title--mastercard {
    margin-left: 60px;
    font-weight: 600; }
    .cards-list__title--mastercard::before {
      background-image: url("../img/master-card.svg");
      background-size: 48px 30px;
